
.btn{
    cursor: pointer;
    width: 200px;
    height: 35px;
    color: rgb(255, 255, 255);
    background: rgba(232, 170, 245, 0.6);
    display: flex;
    justify-content: center;
    border-radius: 20px;
}

@media screen and (max-width: 850px) {
    .btn {
        width: 150px;
    }
}